<script>
import {LMap, LTileLayer, LMarker} from "vue2-leaflet";
import {latLng, icon} from "leaflet";

export default {
  name: "Icon",
  components: {
    LMap,
    LTileLayer,
    LMarker,

  },
  data() {
    return {
      zoom: 13,
      center: latLng(41.3077178, 69.2539164),
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
      attribution:
          '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors',

      icon: icon({
        iconUrl: "data:image/png;base64,",
        iconSize: ['auto', 'auto'],
        iconAnchor: [16, 37]
      }),
      staticAnchor: [16, 37],
      customText: "Foobar",
      iconSize: 64,
      objects: '',
      object_view: '',
      search: ''
    };
  },
  computed: {
    dynamicSize() {
      return [this.iconSize, this.iconSize * 1.15];
    },
    dynamicAnchor() {
      return [this.iconSize / 2, this.iconSize * 1.15];
    }
  },
  methods: {
    handleMarkerClick(item) {
      this.object_view = item
    },
    searchs() {
      this.$http.get(`objects_maps/?name=${this.search}`)
          .then(res => {
            this.objects = res.data

          })
    }
  },
  mounted() {
    this.$http.get('objects_maps/')
        .then(res => {
          this.objects = res.data
        })

  },
};
</script>

